.profile__avatar-image {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-right: 30px;
  object-fit: cover;
  display: block;
}

@media screen and (max-width: 750px) {
  .profile__avatar-image {
    margin: 0 0 26px 0;
  }
}


  .profile__avatar-image:hover {
    opacity: 0;
  }
